import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import StaffSalaryStructurePreview from "./StaffSalaryStructurePreview";

const PayrollSalaryStructureSatff = ({ staff }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.smc_card} onClick={onToggle}>
        <div
          className={style.smc_card_contatiner}
          style={{
            justifyContent: "space-between",
            width: "100%",
          }}
        >
          <div className={style.smc_card_contatiner}>
            <img
              src={
                staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("index_number_1")} {staff?.staffROLLNO ?? ""}
              </p>
            </div>
          </div>
        </div>
      </div>

      {open && (
        <StaffSalaryStructurePreview
          onClose={onToggle}
          ssid={staff?.salary_structure}
          // ssid={"661cbb816691502f8442b153"}
          staff={staff}
        />
      )}
    </>
  );
};

export default PayrollSalaryStructureSatff;
