import { useLocation, useParams, useSearchParams } from "react-router-dom";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import SalaryComponent from "./SalaryComponent/SalaryComponent";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import EmployeeDeduction from "./EmployeeDeduction/EmployeeDeduction";
import EmployerDeduction from "./EmployerDeduction/EmployerDeduction";
import Compliances from "./Compliances/Compliances";

const PayrollSalaryHead = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  return (
    <>
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("d"))}
            parentState={getQuery.state}
            url={`/q/${params.username}/member/staff/payroll?type=config&n=head${tab.urlKey}`}
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginBlock: "0",
        }}
      />
      <UserMemberContentWrapper>
        {searchParams.get("d") === "employee" ? (
          <EmployeeDeduction
            instituteId={instituteId}
            initalUrl="attendance-sheet"
            pid={pid}
          />
        ) : searchParams.get("d") === "employer" ? (
          <EmployerDeduction
            instituteId={instituteId}
            initalUrl="attendance-sheet"
            pid={pid}
          />
        ) : searchParams.get("d") === "compliances" ? (
          <Compliances
            instituteId={instituteId}
            initalUrl="attendance-sheet"
            pid={pid}
          />
        ) : (
          <SalaryComponent
            instituteId={instituteId}
            initalUrl="attendance-sheet"
            pid={pid}
          />
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default PayrollSalaryHead;

const tabList = [
  {
    label: "salary_component",
    id: "salary_component",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "employee_deduction",
    id: "employee_deduction",
    urlKey: "&d=employee",
    groupIs: ["employee"],
  },
  {
    label: "employer_deduction",
    id: "employer_deduction",
    urlKey: "&d=employer",
    groupIs: ["employer"],
  },
  {
    label: "compliances",
    id: "compliances",
    urlKey: "&d=compliances",
    groupIs: ["compliances"],
  },
];
