import { useTranslation } from "react-i18next";
import style from "../../../../FinanceManager/Income/Income.module.css";
import { useState } from "react";
import { usePayrollAddSalaryHead } from "../../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../../Ui/Input/FormInput";
import TileWrapper from "../../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../../Ui/Tab/Tile/TileCard";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../Validation/functions/checkingRequiredField";
const AddSalaryComponent = ({ onClose, onRefetch, pid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    heads_name: "",
    heads_price: "",
    heads_toggle: "",
    heads_percentage: "",
    heads_condition: "",
    heads_type: "SALARY_COMPONENTS",
    heads_parent: "",
  });
  const [payrollAddSalaryHead] = usePayrollAddSalaryHead();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (val) => {
    setState((prev) => ({
      ...prev,
      heads_toggle: val,
    }));
  };
  const onHeadTile = (val) => {
    setState((prev) => ({
      ...prev,
      heads_parent: val,
    }));
  };
  const onAddHeadPerson = () => {
    let validation = requiredField(state, [
      "heads_price",
      "heads_percentage",
      "heads_condition",
    ]);
    if (existingRequiredField(validation) && pid) {
      setDisabled((pre) => !pre);
      payrollAddSalaryHead({
        pid: pid,
        bodyParameter: {
          ...state,
          heads_price: 0,
          heads_percentage: state.heads_percentage
            ? +state.heads_percentage
            : 0,
          heads_toggle:
            state.heads_toggle === "Dependent"
              ? true
              : state.heads_toggle === "Independent"
              ? false
              : "",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("pid", state);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_salary_head")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("salary_head_name")}
              placeholder={t("salary_head_name_placeholder")}
              name={"heads_name"}
              value={state.heads_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["heads_name"]}
            />
            <TileWrapper label="head_type">
              {list_type?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onHeadTile}
                  activeTile={state?.heads_parent}
                />
              ))}
            </TileWrapper>
            <TileWrapper label="dependency">
              {list_depend?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onTile}
                  activeTile={state?.heads_toggle}
                />
              ))}
            </TileWrapper>
            {state.heads_toggle === "Dependent" ? (
              <FormInput
                label={t("heads_percentage")}
                placeholder={t("heads_percentage_placeholder")}
                name={"heads_percentage"}
                value={state.heads_percentage}
                type={"text"}
                onChange={onChange}
                // errorShow={errorField["heads_percentage"]}
              />
            ) : null}

            <CreateButton label="add" onAction={onAddHeadPerson} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddSalaryComponent;

export const list_depend = [
  {
    key: "Dependent",
    name: "dependent",
  },
  {
    key: "Independent",
    name: "independent",
  },
];

export const list_type = [
  {
    key: "ALLOWANCES",
    name: "allowances",
  },
  {
    key: "PERQUISITES",
    name: "perquisites",
  },
];
