import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import { useEffect, useState } from "react";
import { usePayrollOneSalaryStructure } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
const StaffSalaryStructurePreview = ({ staff, onClose, ssid }) => {
  const { t } = useTranslation();

  const [stateTotal, setStateTotal] = useState({
    sal_com: 0,
    employee_ded: 0,
    employar_ded: 0,
    com: 0,
  });

  const { payrollOneSalaryStructure } = usePayrollOneSalaryStructure({
    data: {
      ssid: ssid,
    },
    skip: !ssid,
  });
  useEffect(() => {
    if (payrollOneSalaryStructure?.salary_components?.length) {
      let tp = 0;
      for (let st of payrollOneSalaryStructure?.salary_components) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        sal_com: tp,
      }));
    }

    if (payrollOneSalaryStructure?.employee_deduction?.length) {
      let tp = 0;
      for (let st of payrollOneSalaryStructure?.employee_deduction) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employee_ded: tp,
      }));
    }

    if (payrollOneSalaryStructure?.employar_deduction?.length) {
      let tp = 0;
      for (let st of payrollOneSalaryStructure?.employar_deduction) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employar_ded: tp,
      }));
    }

    if (payrollOneSalaryStructure?.compliances?.length) {
      let tp = 0;
      for (let st of payrollOneSalaryStructure?.compliances) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        com: tp,
      }));
    }
  }, [
    payrollOneSalaryStructure?.salary_components,
    payrollOneSalaryStructure?.employar_deduction,
    payrollOneSalaryStructure?.employee_deduction,
    payrollOneSalaryStructure?.compliances,
  ]);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {`${staff?.staffFirstName ?? ""} ${
                    staff?.staffMiddleName ?? ""
                  } ${staff?.staffLastName ?? ""} `}
                  - {t("salary_structure")}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <section
              className={style.table_container}
              style={{
                margin: "0",
              }}
            >
              <div className={style.table_heading}>
                <h6>{t("particulars")}</h6>
                <h6>{t("amount")}</h6>
              </div>
              <div className={style.table_body}>
                {payrollOneSalaryStructure?.salary_components?.map(
                  (afh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <h6>{afh?.master?.heads_name}</h6>
                      <h6>{afh?.head_amount}</h6>
                    </div>
                  )
                )}
              </div>
              <div className={style.table_body}>
                <div
                  className={style.table_body_list}
                  style={{
                    borderTopColor: "rgba(18, 18, 18, 0.5)",
                    borderTopWidth: "0.5px",
                    borderTopStyle: "solid",
                  }}
                >
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {t("total_earning")}
                  </h6>
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {stateTotal.sal_com}
                  </h6>
                </div>
              </div>
            </section>
            <div className={style.table_body}>
              {payrollOneSalaryStructure?.employee_deduction?.map(
                (gfh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{gfh?.master?.heads_name}</h6>
                    <h6>{gfh?.head_amount}</h6>
                  </div>
                )
              )}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("total_pay")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {stateTotal.sal_com - stateTotal.employee_ded}
                </h6>
              </div>
            </div>
            <div className={style.table_body}>
              {payrollOneSalaryStructure?.compliances?.map((gfh, index) => (
                <div className={style.table_body_list} key={index}>
                  <h6>{gfh?.master?.heads_name}</h6>
                  <h6>{gfh?.head_amount}</h6>
                </div>
              ))}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("net_pay")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {stateTotal.sal_com -
                    stateTotal.employee_ded -
                    stateTotal.com}
                </h6>
              </div>
            </div>

            <div className={style.table_body}>
              {payrollOneSalaryStructure?.employar_deduction?.map(
                (gfh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{gfh?.master?.heads_name}</h6>
                    <h6>{gfh?.head_amount}</h6>
                  </div>
                )
              )}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("ctc")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {stateTotal.sal_com -
                    stateTotal.employee_ded +
                    stateTotal.employar_ded}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default StaffSalaryStructurePreview;
