import SalaryComponentCard from "./SalaryComponentCard";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { usePayrollAllSalaryHead } from "../../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import AddSalaryComponent from "./AddSalaryComponent";
const SalaryComponent = ({ pid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const {
    payrollAllSalaryHead,
    payrollAllSalaryHeadLoading,
    payrollAllSalaryHeadRefetch,
  } = usePayrollAllSalaryHead({
    data: {
      pid: pid,
      page: page,
      limit: 10,
      search: search,
      type: "SALARY_COMPONENTS",
    },
    skip: !pid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (payrollAllSalaryHead?.list?.length) {
      let obj = nextPage(payrollAllSalaryHead?.list?.length);
      setState(() => obj);
    }
  }, [payrollAllSalaryHead?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    payrollAllSalaryHeadRefetch();
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>

      {payrollAllSalaryHead?.list?.map((head, index) =>
        payrollAllSalaryHead?.list?.length === index + 1 ? (
          <div key={head?._id} ref={ref}>
            <SalaryComponentCard head={head} />
          </div>
        ) : (
          <SalaryComponentCard head={head} key={head?._id} />
        )
      )}
      {payrollAllSalaryHeadLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        payrollAllSalaryHead?.list?.length === 0 && (
          <EmptyMessage
            title="No salary components found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        payrollAllSalaryHead?.list?.length === 0 && (
          <EmptyMessage
            title="No salary components."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      )}

      {add && (
        <AddSalaryComponent
          onClose={onToggle}
          onRefetch={onRefetch}
          pid={pid}
        />
      )}
    </>
  );
};

export default SalaryComponent;
