import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../StoreManager/StoreManager.module.css";
const SalaryComponentCard = ({ head }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/member/staff/payroll?type=one-head`}
      state={{
        ...getQuery.state,
        head: head,
      }}
    >
      <div className={style.smc_card}>
        <div className={style.smc_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/category.svg`}
            alt="categor icon"
          />
          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {t("name")}
              {": "}
              {head?.heads_name ?? ""}
            </h6>
            {head?.heads_parent ? (
              <p>
                {t("head_type")} {": "} {head?.heads_parent}
              </p>
            ) : null}
          </div>
        </div>
      </div>
    </Link>
  );
};

export default SalaryComponentCard;
