import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import PayrollSalaryHead from "./PayrollSalaryHead/PayrollSalaryHead";
import PayrollSalaryStructure from "./PayrollSalaryStructure/PayrollSalaryStructure";
const PayrollConfiguration = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/payroll`, {
      state: {
        ...getQuery.state,
        name: "",
        staffId: "",
      },
    });
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>{t("configurations")}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>

        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/member/staff/payroll?type=config${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
        {searchParams.get("n") === "head" ? (
          <PayrollSalaryHead instituteId={instituteId} pid={pid} />
        ) : (
          <PayrollSalaryStructure pid={pid} instituteId={instituteId} />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default PayrollConfiguration;

const tabList = [
  {
    label: "salary_structure",
    id: "salary_structure",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "salary_heads",
    id: "salary_heads",
    urlKey: "&n=head",
    groupIs: ["head"],
  },
];
